
import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Loading from 'vux/src/components/loading/index.vue'

import api from '@/utils/api'
import { formatLink } from '@/utils/utils'
import fjMapImg from '@/assets/mock/fj_map.png'
import defaultAccoutImg from '@/assets/mock/account.png'

export default {
name: 'Matrix',
components: {
ButtonTab,
ButtonTabItem,
Flexbox,
FlexboxItem,
Loading
},
data () {
return {
fjMapImg,
defaultAccoutImg,
cities: [],
currentCityId: 0,
currentArea: 0,
accountList: [],
loading: false
}
},
methods: {
setCities (data) {
this.cities.splice(0)
this.cities = data
},
setAccoutList (data = []) {
this.accountList.splice(0)
this.accountList = data.map(x => {
x.link = formatLink(x.link)
return x
})
},
changeCity (cityId) {
this.currentCityId = cityId
this.getCityAccounts()
},
getCityAccounts () {
if (this.currentArea === 1) {
return this.fetchOffcialAccounts()
}
const currentCity = this.cities.find(x => x.id === this.currentCityId)
this.setAccoutList(currentCity['Wechat-Offcial-Accounts'])
},
changeArea (areaId) {
this.currentArea = areaId
this.getCityAccounts()
},
async fetchData () {
this.loading = true
const data = await api.getMatrixCitys()
this.setCities(data)
const currentCity = this.cities[0]
this.setAccoutList(currentCity['Wechat-Offcial-Accounts'])
this.loading = false
},
async fetchOffcialAccounts () {
this.loading = true
const data = await api.getOffcialAccountList(this.currentCityId, this.currentArea + 1)
this.setAccoutList(data)
this.loading = false
}
},
async mounted () {
await this.fetchData()
this.currentCityId = this.cities[0].id
}
}



